// flex grid
.flex-grid {
  display: flex;
  align-items: center;
  justify-content: center;
}



.one-third.column { 
	flex: 0 1 30%;
}

.one-half.column { 
	flex: 0 1 48%;
}

.columns {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;

  &.is-centered {
    align-items: center;
  }

  .column {

  }
}

.flex-grid .col {
  flex: 1;
}

