.fill-height {
  height: 100%;
}

.content {
  &.is-narrow {
    padding: 4.5rem;
  }
}

.container {
  @include zebra(); 
}

.brand-logo {
  margin: 2em auto;
  max-width: 240px;
  display: block;
}

.room-desk {
  max-width: 600px;
  margin-left: -130px;
  margin-top: 3rem;
}

.position-top-left {
  position: absolute;
  top: 4em;
  left: 2em;
  max-width: 180px;
}

.full-name {
  font-size: 64px;
}

.job-title {
  font-size: 24px;
}

.position-top-right {
  position: absolute;
  top: 2em;
  right: 2em;
  max-width: 240px;
}

.position-bottom-right {
  position: absolute;
  bottom: 4em;
  right: 4em;
  max-width: 220px;
}

// Message
.contact {
  h2 {
    text-align: center;
  }
}

.list-title {
  text-transform: uppercase;
  font-size: 80%;
  font-weight: bold;
}

// Message
.content {

  a {
    text-align: center;
  }
  
  .welcome & {
    
    text-align: center;

    p {
      max-width:920px;
      margin-left:auto;
      margin-right:auto;
    }
  }
  
  .collapse & {
    padding-top: $wrap-padding;
    max-width: none;
    margin: 0 auto;
  }
  
  h2 {
    font-size: 5.65em;
    line-height: 1;
    margin-bottom: 0.35em;
    @include mobile-first($medium) {
      font-size: 2.75em;
    }
    span {
      color: $secondary-color;
    }
  }
  
  h3 {
    margin-bottom: 1em;
  }
  
  p {
    font-family: $sans-serif-2;
    font-weight: 400;
    // font-size: 1.75rem;
    @include mobile-first($medium) {
      font-size: 1.25em;
    }
  }
}

.text-center {
  text-align: center;
}

.text-large {
  font-size: 24px;
  padding-right: 3rem;
}

.graphic {
  position: absolute;
  top: 0;
  left: 0;
  fill: none;
}
.icon {
  color: #ddd;
  font-size: 150%;
}

// Footer
footer {
  .icon {
    color: $paragraph-font-color;
    font-size: 1.5em;
  }

  .icon-copyright {
    font-size: 15px;
    position: relative;
    top: 1px;
  }

  p.copy {
    color: $paragraph-font-color;
    font-family: $sans-serif;
    font-weight: 100;
    font-size: 1em;
  }
}

// Credits
.credits {
  padding: 2em;

  a {
    color: $paragraph-font-color;

    &:hover {
      color: shade($paragraph-font-color, 60%);
    }
  }
}


.test {
  @include prefix(background, none)
}