// Container
.container {
  @include panelize(100%);
  @include clearfix;

  // is-window
  &.size-to-window  {
    @include panelize(100%, 100vh);
  }

  &.animate {
    @include prefix(transition, height 2s ease-in, webkit ms );
    height: 20vh;
  }
}

.row {
  padding: $base-spacing 0;

  &.contain {
    position: relative;
  }
}
