// Typography
$sans-serif: 'Montserrat', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
$sans-serif-2: 'Raleway', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
$serif: 'Palatino Linotype', 'Book Book Antiqua', 'Palatino' , serif;

// Colors
$primary-color: #09f;
$secondary-color: #ffcc2f;
$off-white-color: #f4f4f4;
$grey-color: #cdcdcd;
$heading-font-color: #333;
$paragraph-font-color: #555;
$panel-odd-color: $off-white-color;
$panel-even-color: white;
$action-color: $primary-color;


$header-font-family: $sans-serif;
$base-font-family: $sans-serif-2;

// Font Colors
$base-font-color: #555;
$base-link-color: $action-color;
$base-accent-color: $action-color;

// Font Sizes
$base-font-size: 16px;
$h1-font-size: $base-font-size * 2.25;
$h2-font-size: $base-font-size * 2;
$h3-font-size: $base-font-size * 1.75;
$h4-font-size: $base-font-size * 1.5;
$h5-font-size: $base-font-size * 1.25;
$h6-font-size: $base-font-size;

// Line height
$base-line-height: 1.6;
$header-line-height: 1.25;

// Other Sizes
$base-border-radius: 3px;
$form-border-radius: $base-border-radius;

// Vertical Spacing
$base-spacing: $base-line-height * 1em;

// Dividers
$base-border-color: $grey-color;
$form-border-color: $base-border-color;
$base-border: 1px solid $base-border-color;
$form-border: $base-border;

$button-edge: shade( $primary-color, 50%);
$base-padding: 1.5rem;
$wrap-padding: 1.5rem;

$form-border-color-focus: $base-accent-color;
$form-box-shadow: inset 0 1px 3px rgba(black,0.06);
$form-box-shadow-focus: $form-box-shadow, 0 0 5px rgba(darken($form-border-color-focus, 5), 0.7);

$form-border-color-hover: darken($base-border-color, 15);


$small: 640px;
$medium: 769px;
$large: 1024px;

$nav-bg-color: $primary-color;
$border-radius: 3px;
$max-width: 1004px;


// Neat Overrides
// $column: 90px;
// $gutter: 30px;
// $grid-columns: 12;
// $max-width: em(1088);

// Neat Breakpoints
$medium-screen: em(640);
$large-screen: em(860);

$medium-screen-up: new-breakpoint(min-width $medium-screen 4);
$large-screen-up: new-breakpoint(min-width $large-screen 8);




// hood.css

/// Sets the relative width of a single grid column. The unit used should be the same one used to define `$gutter`. To learn more about golden-ratio() see [Bourbon docs](http://bourbon.io/docs/#golden-ratio). Set with a `!global` flag.
///
/// @type Number (Unit)
$column: golden-ratio(1em, 3) !default;

/// Sets the relative width of a single grid gutter. The unit used should be the same one used to define `$column`. To learn more about golden-ratio() see [Bourbon docs](http://bourbon.io/docs/#golden-ratio). Set with the `!global` flag.
///
/// @type Number (Unit)
$gutter: golden-ratio(1em, 1) !default;

/// Sets the total number of columns in the grid. Its value can be overridden inside a media query using the `media()` mixin. Set with the `!global` flag.
///
/// @type Number (Unitless)
$grid-columns: 12 !default;

/// Sets the max-width property of the element that includes `outer-container()`. To learn more about `em()` see [Bourbon docs](http://bourbon.io/docs/#px-to-em). Set with the `!global` flag.
///
/// @type Number (Unit)
///
$max-width: em(1088) !default;

///Sets the default layout direction of the grid. Can be `LTR` or `RTL`. Set with a `!global` flag.
///
///@type String
$default-layout-direction: LTR !default;







// Default Fonts
$sans-serif: "Source Sans Pro", sans-serif !default;
$sans-serif-2: "Source Sans Pro", sans-serif !default;

$primary-color: #FF7F50 !default;
$primary-color-medium: darken($primary-color, 15%) !default;
$primary-color-dark: darken($primary-color, 25%) !default;

// Primary Colors
$white: #FFFFFF !default;
$subtle-gray: #F6F6F6 !default;
$light-gray: #F2F0F0 !default;
$gray: #D9D9D9 !default;
$medium-gray: #CACACA !default;
$dark-gray: #AAAAAA !default;

// Secondary Colors
$light-yellow: #FFFBED !default;
$red: #FF5050 !default;
$charcoal: #333333 !default;

$messages--status: $primary-color;
$messages--warning: $light-yellow;
$messages--error: $red;

// Theme Colors
$background-color: $subtle-gray !default;
$text-color: $charcoal !default;
$text-description-color: #555555 !default;

// 'Link' Color
$action-color: $primary-color !default;
$action-color--hover: $primary-color-medium !default;
$action-color--focus: $primary-color-medium !default;
$action-color--active: $primary-color-dark !default;

// Lines and Dividers and shadows
$line-color: $medium-gray !default;
$base-border: 1px solid $line-color !default;
$base-corner-radius: 4px !default;
$base-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1) !default;

// H1
$h1-font-family: $sans-serif !default;
$h1-font-size: 32px !default;
$h1-font-weight: 600 !default;
$h1-font-color: $text-color !default;
$h1-font-line-height: initial !default;
$h1-font-margin-bottom: 0.6em !default;

// H2
$h2-font-family: $sans-serif !default;
$h2-font-size: 24px !default;
$h2-font-weight: 600 !default;
$h2-font-color: $text-color !default;
$h2-font-line-height: 1.4em !default;
$h2-font-margin-bottom: 1.2em !default;

// H3
$h3-font-family: $sans-serif !default;
$h3-font-size: 20px !default;
$h3-font-weight: 600 !default;
$h3-font-color: $text-color !default;
$h3-font-line-height: 1.4em !default;
$h3-font-margin-bottom: 0.5em !default;

// Tables
$table-header-bg: $white !default;
$table-row-padding: 0.1em 0.6em !default;
$table-row-bg: $white !default;
$table-row-bg--hover: $light-yellow !default;
$table-link-color: $action-color !default;
$table-link-color--hover: $action-color--hover !default;
$tablesort--asc-img: '../../../../misc/icons/004875/twistie-up.svg';
$tablesort--desc-img: '../../../../misc/icons/004875/twistie-down.svg';

// Forms
$input-text-bg-color: $white !default;
$input-text-bg-color--hover: $white !default;
$input-text-bg-color--focus: $white !default;
$input-select-bg-color: linear-gradient(to bottom, $white, $subtle-gray) !default;
$input-select-bg-color--hover: linear-gradient(to bottom, $subtle-gray, $white) !default;
$input-select-bg-color--focus: linear-gradient(to bottom, $subtle-gray, $white) !default;
$input-max-width: 500px;
$input-height: 40px !default;
$input-height-small: 32px !default;
$textarea-default-height: 80px !default;

// Layout
$layout-container-margin: 0 !default;
$layout-sidebar-first-width: 220px !default;
$layout-sidebar-second-width: $layout-sidebar-first-width !default;

// Spacing
$base-gutter: 2.5rem !default;
$base-spacing: 1.5rem !default;
$base-line-height: 1.5em !default;
$half-spacing: $base-spacing / 2 !default;

// Page
$page-body-bg-color: $white !default;
$page-main-bg-color: $white !default;
$page-header-bg-color: $white !default;
$page-header-title-color: $text-color !default;
$page-header-border-bottom: $base-border !default;

// Pager
$pager-select-bg-color: linear-gradient(to bottom, $white, $subtle-gray) !default;
$pager-select-bg-color--hover: linear-gradient(to bottom, $subtle-gray, $white) !default;
$pager-select-bg-color--active: $action-color--active !default;
$pager-in-between-spacing: $half-spacing / 2 !default;
$pager-button-padding: 0 7px 2px !default;

// Tab Navigation / Secondary Nav
$tabs-border: $base-border !default;
$tabs-bg-color: $subtle-gray !default;
$tabs-bg-color--hover: $light-gray !default;
$tabs-bg-color--focus: $white !default;
$tabs-color: $action-color !default;
$tabs-color--hover: $action-color--hover !default;
$tabs-padding-default: 16px 0 17px 40px !default;

// Button Default
$button-padding-default: 9px 24px 10px 24px !default;
$button-padding-small: 4px 24px !default;
$button-background-color: linear-gradient(to bottom, $white, $subtle-gray)  !default;
$button-background-color--hover: linear-gradient(to bottom, $subtle-gray, $white) !default;
$button-background-color--focus: linear-gradient(to bottom, $subtle-gray, $white) !default;
// -webkit-linear-gradient(top, $subtle-gray, $white)
$button-background-color--active: $light-gray !default;
$button-border-color: $medium-gray !default;

// $drop-button
$dropbutton-background-color: linear-gradient(to bottom, $white, $subtle-gray)  !default;
$dropbutton-background-color--hover: linear-gradient(to bottom, $subtle-gray, $white) !default;
$dropbutton-background-color--focus: linear-gradient(to bottom, $subtle-gray, $white) !default;

// Primary Buttons
$button-primary-background-color: $action-color !default;
$button-primary-background-color--hover: $action-color--hover !default;
$button-primary-background-color--focus: $action-color--focus !default;
$button-primary-background-color--active: $action-color--active !default;

/// Status
$status-pill-padding: 0 24px !default;
$status-pill-border: 5px solid !default;



